.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.nut-bam-len-tren {
  position: fixed;
  bottom: 20px;
  right: 20px;
}
.App-logo-quay-xuoi {
  animation: App-logo-spin-quay-xuoi infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

@keyframes App-logo-spin-quay-xuoi {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}